import * as FreeStyle from 'free-style';
/**
 * We need to do the following to *our* objects before passing to freestyle:
 * - For any `$nest` directive move up to FreeStyle style nesting
 * - For any `$unique` directive map to FreeStyle Unique
 * - For any `$debugName` directive return the debug name
 */

export function ensureStringObj(object) {
  /** The final result we will return */
  var result = {};
  var debugName = '';

  for (var key in object) {
    /** Grab the value upfront */
    var val = object[key];
    /** TypeStyle configuration options */

    if (key === '$unique') {
      result[FreeStyle.IS_UNIQUE] = val;
    } else if (key === '$nest') {
      var nested = val;

      for (var selector in nested) {
        var subproperties = nested[selector];
        result[selector] = ensureStringObj(subproperties).result;
      }
    } else if (key === '$debugName') {
      debugName = val;
    } else {
      result[key] = val;
    }
  }

  return {
    result: result,
    debugName: debugName
  };
} // todo: better name here

export function explodeKeyframes(frames) {
  var result = {
    $debugName: undefined,
    keyframes: {}
  };

  for (var offset in frames) {
    var val = frames[offset];

    if (offset === '$debugName') {
      result.$debugName = val;
    } else {
      result.keyframes[offset] = val;
    }
  }

  return result;
}